.custom-tabs-div{
    padding-right: 1.2rem;
    padding-left: 1.2rem;
    display: flex;
    gap: 16px;
    flex-flow: wrap;
}
.custom-tabs-div p{
    border: 2px solid #dedede;
    padding: 4px 10px;
    cursor: pointer;
    border-radius: 4px;
    margin: 0;
    display: flex;
    align-items: center;  
    gap: 8px;  
    font-weight: 500;
}
.custom-tabs-div p.activeP{
    background-color: var(--primary);
    color: #fff;
    border: 2px solid var(--primary);
}
